*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    color: rgb(25, 255, 255);

}

.workinprogress{
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 10px;
    height: 20vh;
}

.workinprogress p{

    font-family: 'Lato', sans-serif;
    font-size: 20px;

}
.navbar{
    position: fixed;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 60px;
    padding: 0px 20px 0px 20px;
}
.navbar p{
    font-family: 'Noto Sans Mono', monospace;
    
}
